@import 'src/styles/base';
.voucher-item-container {
  background-color: white;
  p {
    font-family: 'Google Sans', sans-serif;
  }
  height: 290px;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  .thumbnail {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
  .cart_icon {
    width: 30px;
    height: 30px;
    path {
      fill: white;
    }
  }
  .merchant_image {
    width: 48px;
    height: 48px;
    border-radius: 24px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .image-section {
    height: 183px;
    position: relative;
    .box-img {
      display: flex;
      height: 180px;
      img {
        object-fit: cover;
      }
    }
    .content-image-section {
      position: absolute;
      top: 0;
      display: flex;
      justify-content: space-between;
      height: 100%;
      width: 100%;
      align-items: flex-end;
      padding: 16px;

      .content-eVoucher {
        height: 100%;
        display: flex;
        flex-direction: column;

        .title-eVoucher {
          padding-top: 16px;
          color: white;
          font-size: 18px;
        }
        .eVoucher-value {
          padding: 12px 0 12px 0;
          font-size: 36px;
          font-weight: bold;
          background: -webkit-linear-gradient(45deg, #1b315f, #4e628b 80%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
        .eVoucher-value-string {
          color: white;
          font-size: 18px;
        }
      }

      .percent-item {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fe5466;
        width: 40px;
        height: 40px;
        border-radius: 50%;

        .percent-value {
          color: white;
        }
      }
    }
  }

  .content-section {
    height: 110px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 6px 20px 10px 20px;

    .eVoucher-name {
      height: 50px;
      display: -webkit-box;
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
      text-overflow: ellipsis;
      overflow: hidden;
      font-size: 18px;
      font-family: 'Google Sans', sans-serif;
      font-weight: 700;
    }

    .bottom-section {
      display: flex;
      justify-content: space-between;

      .bottom-left {
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        .price-original {
          text-decoration: line-through;
          font-size: 12px;
          font-weight: bold;
          color: #000000;
        }

        .price-section {
          display: flex;
          align-items: center;
          .price {
            color: #ff4355;
            font-size: 18px;
            padding-left: 8px;
          }
        }
      }

      .bottom-right {
        .customTitle {
          font-size: 17px;
          font-family: 'Google Sans', sans-serif;
          font-weight: 400;
        }
      }
    }
  }
}
