.request-modal-container {
  padding-right: 2em;
  padding-left: 1em;
  overflow: scroll;
  .header-title {
    font-size: 24px;
    font-weight: bold;
    padding-bottom: 2em;
  }

  .row-section {
    display: flex;
    padding-bottom: 2em;

    .input {
      border: 1px solid #dfe2e7;
      border-radius: 4px;
      width: 100%;
      height: 3em;
      padding-left: 1em;
    }

    .input-branch {
      background: #f6f9ff;
    }

    .label {
      font-size: 15px;
      font-weight: bold;
      padding-bottom: 1em;
    }

    .label-number {
      font-size: 15px;
      font-weight: bold;
    }

    .number {
      font-size: 15;
      font-weight: bold;
      color: #c4a732;
    }

    .inline {
      display: inline-block;
    }

    .right-content {
      padding-left: 1em;
      width: 100%;
    }

    .credit-section {
      display: flex;
      align-items: center;

      .orderer {
        padding-left: 1em;
        font-size: 15;
      }

      .orderer-name {
        font-size: 15;
        font-weight: bold;
        color: #c4a732;
      }
    }
  }

  .folder-icon {
    height: 30px;
    width: 30px;
  }

  .fork-icon {
    height: 30px;
    width: 30px;
    object-fit: contain;
  }

  .align-center {
    align-items: center;
  }

  .between {
    justify-content: space-between;
  }
}

.success-modal-container {
  padding: 3em 2em 2.5em 2em;
  text-align: center;
  .success-txt {
    font-size: 23px;
    color: #37c86f;
    padding: 1em 0 1em 0;
  }

  .address-txt {
    color: #1b315f;
    padding-bottom: 1em;
  }
}

@media only screen and (max-height: 800px) {
  .request-modal-container {
    height: 500px;
  }
}

@media only screen and (max-width: 530px) {
  .request-modal-container {
    width: 100%;
    padding-right: 1em;
    height: 570px;
    .row-section {
      .credit-section {
        .orderer {
          padding-left: 1em;
        }
      }
    }
  }
}

@media only screen and (max-width: 420px) {
  .request-modal-container {
    width: 100%;
    padding-right: 1em;
    height: 570px;
    .row-section {
      .credit-section {
        .orderer {
          padding-left: 1.5em;
        }
      }
    }
  }
}

@media only screen and (max-width: 375px) {
  .request-modal-container {
    width: 100%;
    padding-right: 1em;
    .header-title {
      font-size: 17px;
    }
    .row-section {
      .input-branch {
        height: 40px;
        font-size: 15px;
      }
      .input {
        height: 40px;
        font-size: 15px;
      }
      .credit-section {
        .credit-icon {
          height: 30px;
          width: 30px;
        }
        .orderer {
          padding-left: 2em;
          font-size: 13px;
        }
        .orderer-name {
          font-size: 14px;
        }
      }
      .label {
        font-size: 15px;
      }
      .inline {
        font-size: 14px;
      }
      .label-number {
        font-size: 14px;
      }
    }
    ::placeholder {
      font-size: 15px;
    }
  }
}

@media only screen and (max-width: 360px) {
  .request-modal-container {
    width: 100%;
    padding-right: 1em;
    .row-section {
      .credit-section {
        .orderer {
          padding-left: 1.5em;
        }
      }
    }
  }
}

@media only screen and (max-width: 320px) {
  .request-modal-container {
    width: 100%;
    padding-right: 1em;
    height: 500px;
    .header-title {
      font-size: 15px;
    }
    .row-section {
      .input-branch {
        height: 30px;
        font-size: 10px;
      }
      .input {
        height: 30px;
        font-size: 10px;
      }
      .credit-section {
        .credit-icon {
          height: 30px;
          width: 30px;
        }
        .orderer {
          padding-left: 2em;
          font-size: 11px;
        }
        .orderer-name {
          font-size: 12px;
        }
      }
      .label {
        font-size: 11px;
      }
      .inline {
        font-size: 12px;
      }
      .label-number {
        font-size: 12px;
      }
    }
    ::placeholder {
      font-size: 10px;
    }
  }
}

@media only screen and (max-width: 280px) {
  .request-modal-container {
    width: 100%;
    padding-right: 1em;
    height: 570px;
    .header-title {
      font-size: 16px;
    }
    .row-section {
      .credit-section {
        .credit-icon {
          height: 30px;
          width: 30px;
        }
        .orderer {
          padding-left: 2em;
          font-size: 10px;
        }
        .orderer-name {
          font-size: 11px;
        }
      }
      .label {
        font-size: 12px;
      }
      .inline {
        font-size: 12px;
      }
      .label-number {
        font-size: 12px;
      }
    }
    ::placeholder {
      font-size: 13px;
    }
  }
}
