.slider-content {
  .slick-arrow slick-next {
    color: 'red';
  }

  .image_show {
    height: 420px;
    object-fit: cover;
  }
  .slick-dots {
    bottom: 0.5vw;
    li.slick-active button:before {
      color: var(--primary-peach);
    }
    li button:before {
      font-size: 12px;
      opacity: 1;
      color: var(--primary-gray-blur);
    }
    li button:before {
      font-size: 12px;
      opacity: 1;
      color: var(--primary-gray-blur);
    }
  }
  .slick-next {
    right: -60px;
  }
  .slick-prev {
    left: -60px;
  }
  .slick-next:before {
    display: none;
  }
  .slick-prev:before {
    display: none;
  }

  @media screen and (max-width: 600px) {
    .image_show {
      height: 200px;
      object-fit: cover;
    }
  }
}
