.alert-success {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  .title {
    margin-top: 28px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 30px;
    color: #ff4355;
  }
  .subtitle {
    margin-bottom: 36px;
    margin-top: 8px;
    margin-left: 40px;
    margin-right: 40px;
    font-size: 20px;
    // max-width: 300px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    color: #243358;
  }
  .custom-button {
    cursor: pointer;
    background-color: var(--primary-pink);
    box-shadow: 1px 5px 5px rgba(254, 84, 102, 0.15);
    border-radius: 20px;
    width: 70%;
    height: 44px;
    color: white;
    font-size: 18px;
    line-height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Google Sans', sans-serif;
    align-self: center;
  }
}

@media only screen and (max-width: 650px) {
  .alert-success {
    .title {
      font-size: 25px !important;
    }
    .subtitle {
      font-size: 20px !important;
    }
  }
}

@media only screen and (max-width: 430px) {
  .alert-success {
    width: 100%;
  }
}
@media only screen and (max-width: 420px) {
  .alert-success {
    width: fit-content;
  }
}
@media only screen and (max-width: 411px) {
  .alert-success {
    width: fit-content;
    .icon-success {
      height: 90px;
      width: 90px;
    }
  }
}
@media only screen and (max-width: 360px) {
  .alert-success {
    width: 100%;
  }
}
@media only screen and (max-width: 320px) {
  .alert-success {
    .title {
      font-size: 22px !important;
    }
    .subtitle {
      font-size: 17px !important;
    }
  }
}
@media only screen and (max-width: 280px) {
  .alert-success {
    .title {
      font-size: 18px !important;
    }
    .subtitle {
      font-size: 15px !important;
    }
  }
}
