.login-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 600px;
  text-align: center;
  padding: 15px 35px;
  .title-button {
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 20px;
  }
  .ic-default-avatar {
    width: 96px;
    height: 96px;
    margin-bottom: 10px;
    path {
      fill: #c4c4c4;
    }
  }

  .login_error {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
    margin-bottom: 8px;
    color: #fe5466;
  }
  .error {
    margin-left: 20px;
  }
  .login_form_register_button {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 20px;
    color: #0443c6;
    cursor: pointer;
  }
  .login_form_register_title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 20px;
  }

  .login_form_password {
    margin-left: 20px;
  }

  .login_form_phone_number {
    margin-left: 20px;
  }
  .title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 40px;
  }

  p {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    font-size: 20px;
    max-width: 382px;
  }
  .form-label {
    margin-top: 10px;
  }

  .mar-b-15 {
    margin-top: 17px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    font-size: 20px;
    color: #0443c6;
  }

  .mar-b-20 {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    font-size: 20px;
    color: black;
  }
  .forgot-password {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
  }
  .form-builder {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0;
    margin-right: 0;
  }
  .form-widget {
    padding: 0;
  }
}
@media only screen and (max-width: 700px) {
  .login-modal {
    width: 500px;
    .forgot-password {
      font-size: 20px;
      padding-bottom: 8px;
    }
  }
}
@media only screen and (max-width: 650px) {
  .login-modal {
    width: 500px;
    .forgot-password {
      font-size: 20px;
      padding-bottom: 8px;
    }
  }
}

@media only screen and (max-width: 550px) {
  .login-modal {
    width: 420px;
    padding: 10px;
    .error {
      font-size: 14px;
    }
    .form-label {
      font-size: 18px;
    }
    .forgot-password {
      padding-block: 9px;
    }
    .mar-b-20 {
      font-size: 18px;
    }
  }
}

@media only screen and (max-width: 430px) {
  .login-modal {
    width: 330px;
    .error {
      font-size: 13px;
    }
    ::placeholder {
      font-size: 13px;
    }
  }
}
@media only screen and (max-width: 420px) {
  .login-modal {
    width: 330px;
    s .MuiInputBase-input {
      width: 100%;
    }
    .title-button {
      font-size: 16px;
    }
    .login_title {
      font-size: 23px;
      margin-bottom: 20px;
    }
    .form-label {
      font-size: 14px;
    }
    .forgot-password {
      padding-bottom: 8px;
      font-size: 17px;
    }
    .mar-b-15 {
      font-size: 15px;
    }
    .mar-b-20 {
      font-size: 16px;
    }
    .login_form_register_title {
      font-size: 15px;
    }
    .login_form_register_button {
      font-size: 15px;
    }
  }
}
@media only screen and (max-width: 411px) {
  .login-modal {
    width: 100%;
    .form-input {
      width: auto;
    }
    .login_title {
      font-size: 17px;
      margin-bottom: 20px;
    }
    .form-label {
      font-size: 14px;
    }
    .forgot-password {
      padding-bottom: 8px;
      font-size: 16px;
    }
    .title-button {
      font-size: 15px;
    }
    .mar-b-15 {
      font-size: 15px;
    }
    .mar-b-20 {
      font-size: 14px;
    }
    .login_form_register_title {
      font-size: 15px;
    }
    .login_form_register_button {
      font-size: 15px;
    }
  }
}
@media only screen and (max-width: 360px) {
  .login-modal {
    width: 100%;
    padding: 15px 25px;
    .login_title {
      font-size: 17px !important;
      margin-bottom: 20px;
    }
    .form-label {
      font-size: 12px;
    }
    .forgot-password {
      font-size: 14px;
      padding-bottom: 8px;
    }
    .mar-b-15 {
      font-size: 15px;
    }
    .mar-b-20 {
      font-size: 14px;
    }
    .login_form_register_title {
      font-size: 15px;
    }
    .login_form_register_button {
      font-size: 15px;
    }

    .title-button {
      font-size: 14px;
    }
  }
}
@media only screen and (max-width: 320px) {
  .login-modal {
    width: 100%;
    .MuiInputBase-input {
      width: 90%;
    }
    .eye-password {
      width: 15px;
      height: 15px;
    }
    .forgot-password {
      font-size: 14px;
      padding-block: 10px;
    }
    .mar-b-20 {
      font-size: 15px;
    }
    .title {
      margin-bottom: 10px;
      font-size: 18px !important;
    }
    .login_form_register_title {
      font-size: 14px;
    }
    .login_form_register_button {
      font-size: 14px;
    }
  }
}
@media only screen and (max-width: 280px) {
  .login-modal {
    width: 100%;
    padding: unset;
    ::placeholder {
      font-size: 10px;
    }
    .ic-default-avatar {
      width: 56px;
      height: 56px;
    }
    .form-label {
      font-size: 12px;
    }
    .login_form_phone_number {
      width: auto;
    }
    .title-button {
      font-size: 12px;
    }
    .login_form_password {
      width: auto;
    }
    .MuiInputBase-input {
      width: 100%;
      font-size: 12px;
    }
    .eye-password {
      width: 15px;
      height: 15px;
    }
    .forgot-password {
      font-size: 14px;
      padding-block: 10px;
    }
    .mar-b-15 {
      font-size: 14px;
    }
    .mar-b-20 {
      font-size: 13px;
    }
    .title {
      margin-bottom: 10px;
      font-size: 15px;
    }
    .login_form_register_title {
      font-size: 12px;
    }
    .login_form_register_button {
      font-size: 12px;
    }
  }
}
