.button-container {
  width: 134px;
  height: 34px;
  border: none;
  background: #ff4355;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  .title-button {
    font-family: 'Google Sans', sans-serif;
    font-weight: 600;
    font-size: 17px;
    display: inline-block;
  }

  .icon {
    padding-right: 6px;
  }
}

.button-container:hover {
  opacity: 0.5;
  transition: 0.5;
}

.button-container:focus {
  outline: none;
}
