.modal_address_booking_container {
  padding-right: 2em;
  padding-left: 2em;
  position: relative;
  overflow: scroll;
    .counter-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .header-title {
    font-size: 24px;
    font-weight: bold;
    padding-bottom: 20px;
  }
  .input {
    margin-top: 10px;
    border: 1px solid #dfe2e7;
    border-radius: 4px;
    width: 100%;
    height: 3em;
    padding-left: 1em;
    font-size: 1.2rem;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
  }
  .row-section {
    display: flex;
    align-items: center;

    .input-branch {
      background: #f6f9ff;
    }

    .label {
      margin-left: 10px;
      display: flex;
      align-items: center;
      font-size: 15px;
      font-weight: bold;
    }

    .label-number {
      font-size: 15px;
      font-weight: bold;
    }

    .number {
      font-size: 15;
      font-weight: bold;
      color: #c4a732;
    }

    .inline {
      display: inline-block;
    }

    .right-content {
      width: 100%;
    }

    .credit-section {
      margin-top: 20px;
      margin-bottom: 20px;
      display: flex;
      align-items: center;

      .orderer {
        padding-left: 1em;
        font-size: 15;
      }

      .orderer-name {
        font-size: 15;
        font-weight: bold;
        color: #c4a732;
      }
    }
  }

  .align-center {
    align-items: center;
  }
  .booking_time {
    cursor: pointer;
  }
  .between {
    justify-content: space-between;
  }
  .age_title {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    text-align: center;
    font-weight: 400;
    font-size: 15px;
  }
  .age_description {
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: 'Google Sans', sans-serif;
    text-align: center;
    font-weight: 300;
    font-size: 12px;
  }

  .time_picker_container {
    overflow: scroll;
    position: absolute;
    top: 360px;
    height: 200px;
    padding: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    .time_text {
      cursor: pointer;
      padding: 5px 100px 5px 100px;
    }
  }

  .branch_piker_container {
    overflow: scroll;
    position: absolute;
    top: 360px;
    max-height: 200px;
    padding: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    .branch_address {
      cursor: pointer;
      padding: 5px 10px 5px 10px;
    }
  }
  .calendar_custom_container {
    max-width: 520px;
    position: absolute;
    top: 265px;
    padding: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .error {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    color: #ff4355;
    font-size: 15px;
    margin-top: 8px;
  }
}

.success-modal-container {
  padding: 3em 2em 2.5em 2em;
  text-align: center;
  .success-txt {
    font-size: 23px;
    color: #37c86f;
    padding: 1em 0 1em 0;
  }

  .address-txt {
    color: #1b315f;
    padding-bottom: 1em;
  }
}
@media only screen and (max-height: 800px) {
  .modal_address_booking_container {
    height: 500px;
  }
}

@media only screen and (min-width: 800px) {
  .modal_address_booking_container {
    width: 650px;
    .custom-button {
      min-width: unset;
