.order-item-container {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-bottom: 2em;
  border-radius: 5px;
  .order-item-left {
    .img-order-item {
      height: 100%;
      width: 100%;
    }
  }

  .order-item-right {
    padding: 1em 3em 1em 2em;
    .header-right {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 1em;
      .title-section {
        display: flex;
        align-items: center;

        .title-header {
          font-family: 'Google Sans', sans-serif;
          font-weight: 700;
          font-size: 2rem;
          padding-left: 1em;
        }
      }
    }
  }
  .order_button {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
  }
  .address-txt {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    padding-bottom: 1em;
    font-size: 1.5rem;
  }
  @media screen and (max-width: 600px) {
    .order-item-right {
      padding: 8px 12px;
    }
  }
}
