.drawer_container {
  .drawer a {
    font-size: 14px;
  }

  .avatar {
    display: inline-block;
    margin-right: 5px;
    height: 24px;
    width: 24px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
  }

  .fa {
    margin-right: 10px;
    width: 15px;
  }

  .cate_logo {
    margin-right: 10px;
    max-width: 28px;
    height: 28px;
    font-size: 28px !important;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    object-fit: cover;
  }
  .link {
    font-size: 14px;
  }

  .category_name {
    display: flex;
    align-items: center;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    color: #000;
    font-weight: 400;
  }
  .user_name {
    display: flex;
    align-items: center;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    color: #000;
    font-weight: 400;
  }

  .logout_title {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    color: #000;
    opacity: 0.5;
    font-weight: 400;
  }

  .login_title {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    color: #000;
    font-weight: 600;
  }

  .logout_icon {
    width: 25px;
    height: 25px;
    path {
      fill: #adb1b9;
    }
  }
  .logout_container {
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 4px 16px;
    border-radius: 5px;
  }
  .logout_container:hover {
    background-color: #e7e8ea;
  }
  .logout_container:hover .logout_icon {
    path {
      fill: #253358;
    }
  }

  .credit_card_container {
    display: flex;
    cursor: pointer;
    align-items: center;
    padding: 4px 16px;
    border-radius: 5px;
  }
  .credit_card_container:hover {
    background-color: #e7e8ea;
  }
  .credit_card_container:hover .credit_card_icon {
    path {
      fill: #253358;
    }
  }

  .credit_card_title {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    font-weight: 400;
    color: #000;
    opacity: 0.5;
  }

  .transaction_container {
    display: flex;
    cursor: pointer;
    align-items: center;
    padding: 4px 16px;
    border-radius: 5px;
  }
  .transaction_container:hover {
    background-color: #e7e8ea;
  }
  .transaction_container:hover .transaction_icon {
    path {
      fill: #253358;
    }
  }

  .transaction_title {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    font-weight: 400;
    color: #000;
    opacity: 0.5;
  }

  .transaction_title:hover {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    font-weight: 400;
    color: #243358;
    opacity: 1;
  }

  .transaction_icon {
    width: 25px;
    height: 25px;
  }
}
