.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 8px;
  padding: 15px;
  overflow-y: auto;
  .modal-header {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}
@media only screen and (max-width: 430px) {
  .modal-content {
    width: 80%;
    left: 50%;
    max-height: 120vh;
  }
}
@media only screen and (max-width: 375px) {
  .modal-content {
    width: 80%;
    left: 50%;
    max-height: 120vh;
  }
}

