.force_user_login {
  max-width: 500px;
  text-align: center;

  .title {
    margin-top: 28px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 30px;
    color: #ff4355;
  }
  .subtitle {
    margin-bottom: 36px;
    margin-top: 8px;
    font-size: 20px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    color: #243358;
  }
}
@media only screen and (max-width: 700px) {
  .force_user_login {
    width: 500px;
  }
}
@media only screen and (max-width: 650px) {
  .force_user_login {
    width: 430px;
  }
}

@media only screen and (max-width: 550px) {
  .force_user_login {
    width: 350px;
    .subtitle {
      font-size: 23px !important;
    }
  }
}

@media only screen and (max-width: 430px) {
  .force_user_login {
    width: 330px;
  }
}
@media only screen and (max-width: 420px) {
  .force_user_login {
    width: fit-content;
  }
}
@media only screen and (max-width: 411px) {
  .force_user_login {
    width: fit-content;
  }
}
@media only screen and (max-width: 360px) {
  .force_user_login {
    width: 100%;
  }
}
@media only screen and (max-width: 320px) {
  .force_user_login {
    width: 100%;
    .subtitle {
      font-size: 20px !important;
    }
  }
}
@media only screen and (max-width: 280px) {
  .force_user_login {
    width: 100%;
    .title {
      font-size: 12px;
    }
    .subtitle {
      font-size: 17px !important;
    }
  }
}
