.reset-password-modal {
  text-align: center;
  .fill-phone-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .recaptcha {
    display: flex;
    justify-content: center;
    width: 100%;
  }
  .box-subtitle {
    display: flex;
    flex-wrap: wrap;
    width: 300px;
  }
  .input-group {
    padding: 12px 12px 0px 12px;
    border-bottom: 1px solid #a6aebb;
  }
  .box-button {
    width: 100%;
  }
  .phone-input {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    border: none;
    padding: 0 20px;
    input {
      &:focus {
        box-shadow: none;
      }
    }
  }
  .fill-code-form {
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    .custom_button {
      margin-top: 32px;
    }
  }
  .title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 600;
    font-size: 18px;
  }
  .subtitle {
    margin-bottom: 36px;
    margin-top: 8px;
    font-size: 15px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
  }
  .form-fill-code {
    width: 100%;
  }

  .display-code {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    width: 40px;
    height: 40px;
    border: 1px solid #a6aebb;
    z-index: 2;
  }

  .input_code {
    position: absolute;
    top: 150px;
    width: 0px;
    height: 0px;
    z-index: -1;
  }
  .fill-phone-form {
    padding: 0px 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .input-list {
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;

    input {
      width: 35px;
      height: 35px;
      border: 1px solid #a6aebb;
      text-align: center;
    }
  }
  .input-item {
    margin-right: 15px;
    :focus {
      outline: none;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  button {
    margin-top: 15px;
  }
  .error {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    color: #ff4355;
    font-size: 15px;
  }
  .resend_active {
    cursor: pointer;
    margin-top: 32px;
    margin-bottom: 32px;
    font-family: 'Google Sans', sans-serif;
    color: #27ae60;
  }

  .resend_active:hover {
    text-decoration: underline;
  }

  .resend_inactive {
    margin-top: 32px;
    margin-bottom: 32px;
    font-family: 'Google Sans', sans-serif;
    color: #a6aebb;
  }
  .error {
    margin: 0;
  }

  .form-widget {
    flex-direction: column;
  }
  .form-label {
    width: 100%;
    font-size: 18px;
    margin-bottom: 10px;
  }

  .create-account-form {
    max-width: 450px;
    padding: 0 30px;
    button {
      margin-right: auto;
      margin-left: auto;
    }
    .title {
      font-size: 30px;
      margin-bottom: 20px;
    }
    .description {
      text-align: center;
      font-size: 15px;
      font-weight: 500;
      color: #243358;
      a {
        color: #0774e8;
      }
    }
  }

  .update-info-form {
    width: 570px;

    button {
      margin-right: auto;
      margin-left: auto;
    }
  }
  .ic-default-avatar {
    path {
      fill: #c4c4c4;
    }
  }
  .complete-form {
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    .complete-icon-wrapper {
      box-sizing: content-box;
      border-radius: 50%;
      width: 90px;
      height: 90px;
      border: 25px solid rgba($color: #37c86f, $alpha: 0.05);
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba($color: #37c86f, $alpha: 0.1);
      margin: 0 auto;
      margin-bottom: 20px;
    }

    .title {
      color: #37c86f;
      font-size: 24px;
      font-weight: 500;
      margin-bottom: 10px;
    }

    .subtitle {
      font-size: 15px;
      color: #243358;
      width: 300px;
    }
  }
  @media only screen and (max-width: 900px) {
    .create-account-form {
      padding: 0 13px;
      width: max-content;
    }
  }

  @media only screen and (max-width: 700px) {
    .title {
      font-size: 20px !important;
    }
  }

  @media only screen and (max-width: 650px) {
    .subtitle {
      font-size: 15px !important;
    }
  }

  @media only screen and (max-width: 550px) {
    .fill-code-form {
      width: 400px;
    }
  }
  @media only screen and (max-width: 530px) {
    .create-account-form {
      padding: 0 13px;
      width: 370px;
    }
  }
  @media only screen and (max-width: 420px) {
    .title {
      font-size: 17px !important;
    }
    .create-account-form {
      padding: 0 13px;
      width: 100%;
    }
  }
  @media only screen and (max-width: 430px) {
    .fill-phone-form {
      align-items: unset;
    }
    .fill-code-form {
      width: 100%;
    }
    .recaptcha {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      width: 370px;
    }
    .fill-phone-form {
      padding: unset;
    }
    .box-subtitle {
      width: 280px;
    }
    .subtitle {
      font-size: 14px !important;
    }
    .display-code {
      font-size: 19px;
      width: 38px;
      height: 38px;
    }
  }
  @media only screen and (max-width: 420px) {
    .recaptcha {
      width: 370px;
      -webkit-transform: scale(0.8);
      transform: scale(0.8);
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
    }
    .create-account-form {
      padding: 0 13px;
    }
  }

  @media only screen and (max-width: 410px) {
    .recaptcha {
      width: 350px;
    }
    .create-account-form {
      padding: 0 13px;
    }
  }
  @media only screen and (max-width: 360px) {
    .fill-phone-form {
      align-items: unset;
    }
    .fill-code-form {
      width: 100%;
    }
    .recaptcha {
      -webkit-transform: scale(0.7);
      transform: scale(0.7);
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      width: 360px;
    }
    .box-subtitle {
      width: 240px;
    }
    .subtitle {
      font-size: 12px !important;
    }
    .display-code {
      font-size: 18px;
      width: 30px;
      height: 30px;
    }
  }
  @media only screen and (max-width: 320px) {
    .fill-phone-form {
      align-items: unset;
    }
    .fill-code-form {
      width: 100%;
    }
    .create-account-form {
      padding: 0 20px;
    }
    .recaptcha {
      -webkit-transform: scale(0.7);
      transform: scale(0.7);
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      width: 300px;
    }
    .box-subtitle {
      width: 200px;
    }
    .subtitle {
      font-size: 10px !important;
    }
    .display-code {
      font-size: 15px;
      width: 25px;
      height: 25px;
    }
    .phone-input {
      ::placeholder {
        font-size: 13px;
      }
    }
  }
  @media only screen and (max-width: 280px) {
    .fill-phone-form {
      align-items: unset;
    }
    .fill-code-form {
      width: 100%;
    }
    .create-account-form {
      padding: 0 15px;
    }
    .recaptcha {
      -webkit-transform: scale(0.7);
      transform: scale(0.7);
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      width: 280px;
    }
    button {
      margin-top: unset;
    }
    .custom_button {
      min-width: unset;
    }
    .box-subtitle {
      width: 200px;
    }
    .subtitle {
      font-size: 10px !important;
    }
    .display-code {
      font-size: 15px;
      width: 22px;
      height: 22px;
    }
  }
}
