@import 'src/styles/base';

.counter_input_container {
  display: flex;
  height: 34px;
  overflow: hidden;
  border-radius: 4px;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  // background: #f5f6f8;

  .button {
    width: 40px;
    height: 100%;
    font-size: 1.4rem;
    border: 1px solid #d5d6db;
  }
  .button:active {
    opacity: 0.5;
    border: none;
    outline: none;
  }

  .value {
    width: 40px;
    background: white;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #d5d6db;
    border-bottom: 1px solid #d5d6db;
    border-left: none;
    border-right: none;
    text-align: center;
  }
  @media screen and(max-width:600px) {
    .button {
      width: 20px;
    }
  }
}
