.search_container {
  display: flex;
  flex: 1;
  // max-width: 560px;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #c4c4c4;
  box-sizing: border-box;
  border-radius: 5px;
  background: #f5f6f8;
  .input_search_container {
    flex: 1;
  }

  .input_search_text {
    border: none;
    width: 100%;
    font-size: 16px;
    padding-left: 20px;
    font-family: 'Google Sans', sans-serif;
  }
  .icon_search {
    width: 25px;
    height: 25px;
  }

  .icon_search_left {
    path {
      fill: #a6aebb;
    }
    width: 15px;
    height: 15px;
  }
  .btn_search {
    width: 63px;
    background-color: #eabfb9;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  @media screen and (max-width: 960px) {
    .icon_search {
      width: 20px;
      height: 20px;
    }

    .btn_search {
      width: 40px;
    }
  }
}
.input-container {
  padding-left: 10px;
  background-color: transparent;
  border: 0px solid;
  height: 30px;
  // width: 260px;
  flex: 1;
}
.input-container:focus {
  outline: none;
}
@media screen and (max-width: 600px) {
  .input-container {
    height: 40px;
  }
}
.btn_search {
  width: 2.375rem;
  height: 2.375rem;
  line-height: 2.375rem;
  text-align: center;
  color: #aaa;
  cursor: pointer;
  border: none;
  padding: 0;
  outline: inherit;
  background-repeat: no-repeat;
  background-position: 50%, 50%;
  background-size: 25px;
}

.inputRoot {
  flex: 1;
  border: solid 1px #f5f6f8;
  background: #f5f6f8;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
}

.inputRoot::before,
.inputRoot::after {
  display: none !important;
}

.inputInput {
  display: block;
  width: 100%;
  padding: 10px;
  padding-left: 40px;
  font-size: 15px;
  line-height: 15px;
}

.suggestion {
  display: 'block';
}

.suggestionsList {
  margin: 0;
  padding: 0 8px 0 8px;
}

.suggestion span {
  color: #a6aebb;
}

.suggestion span mark {
  color: #162a4c;
}

.suggest-item {
  color: #a6aebb;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5rem;
  padding: 1em 0 1em 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}

.auto-suggest-container {
  max-width: 100%;
}

.dropdown_search {
  // display: none;
  position: absolute;
  background-color: #ffffff;
  width: 31.5em;
  max-height: 500px;
  border: 1px solid #dfe2e7;
  -webkit-box-shadow: 0 10px 30px rgba(61, 56, 56, 0.1); /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
  -moz-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); /* Firefox 3.5 - 3.6 */
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  z-index: 1;
  top: 6em;
  p {
    // padding: 10px 16px 10px 16px;
    // font-size: 18px;
    font-family: 'Google Sans', sans-serif;
    // text-align: center;
  }
}

.dropdown-item {
  padding: 0 16px 0 16px;
}

.dropdown-item:hover {
  background-color: #eee;
}
