.payment-cart-success-container {
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: scroll;
  padding: 30px;
  justify-content: space-between;
  .title-text {
    text-align: center;
    font-family: 'Google Sans', sans-serif;
    font-size: 23px;
    color: #37c86f;
    margin-top: 21px;
    font-weight: bold;
  }
  .title-text-fail {
    font-size: 23px;
    color: #fe5466;
    margin-top: 21px;
    font-weight: bold;
  }
  .detail-text {
    font-family: 'Google Sans', sans-serif;
    font-size: 17px;
    color: #000000;
    margin-top: 22px;
    text-align: center;
    .point {
      font-size: 17px;
      color: #000000;
      font-weight: bold;
    }
  }
  .footer-section {
    margin-top: 33px;
  }
  // .content-section {
  //   margin-top: 20px;
  //   margin-bottom: 20px;
  // }

  .list_voucher_header {
    margin-top: 20px;
    display: flex;
    align-items: center;
  }
  .list_voucher_header_title {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 17px;
  }
  .temporary_money_title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 600;
    font-size: 17px;
  }

  .temporary_money_content {
    font-family: 'Google Sans', sans-serif;
    font-weight: 600;
    font-size: 17px;
  }
  .promotion_code_title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
  }
  .voucher_item {
    display: flex;
    margin-bottom: 10px;
  }
  .voucher_item_image {
    width: 78px;
    height: 50px;
    border-radius: 4px;
  }
  .voucher_item_quantity {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    margin-left: 16px;
    width: 30px;
    font-size: 15px;
  }

  .voucher_item_name {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    margin-left: 16px;
    font-size: 15px;
    flex: 1;
  }
  .voucher_item_price {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    margin-left: 10px;
    font-size: 15px;
  }
  .line {
    width: 100%;
    height: 1px;
    background-color: #eabfb9;
  }
  .temporary_money_container {
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
  }
  .discount_money_container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
  }
  .payment_method_container {
    display: flex;
    margin-top: 20px;

    justify-content: space-between;
  }
  .payment_method_title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
  }

  .payment_method_content {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
    color: #fe5466;
  }
  .discount_money_content {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
    text-decoration-line: line-through;
  }

  .shipping_money_content {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
  }

  .total_money_container {
    margin-top: 20px;

    display: flex;
    justify-content: space-between;
  }
  .total_money_title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 17px;
  }

  .total_money_content {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    margin-left: 20px;
    font-size: 17px;
    color: #fe5466;
  }
}

@media only screen and (max-height: 850px) {
  .payment-cart-success-container {
    height: 500px;
  }
}

@media only screen and (max-width: 420px) {
  .payment-cart-success-container {
    width: auto;
    padding: 10px;
    .icon-container {
      svg {
        width: 100px;
        height: 100px;
      }
    }
    .voucher_item {
      flex-direction: column;
      .voucher_item_image {
        width: 100%;
      }
      p {
        margin-left: unset;
        margin-top: 6px;
      }
    }
    .total_money_container {
      .total_money_content {
        margin-left: unset;
        margin-right: 10px;
      }
    }
  }
}

@media only screen and (max-width: 360px) {
  .payment-cart-success-container {
    .payment_method_title {
      font-size: 16px;
    }
    .payment_method_content {
      font-size: 17px;
    }
    .total_money_container {
      .total_money_content {
        font-size: 20px;
      }
      .total_money_title {
        font-size: 18px;
      }
    }
    .icon-container {
      svg {
        width: 80px;
        height: 80px;
      }
    }
    .title-text {
      font-size: 16px;
    }
    .detail-text {
      font-size: 15px !important;
    }
    .voucher_item {
      flex-direction: column;
      p {
        margin-top: 4px;
      }
    }
  }
}

@media only screen and (max-width: 320px) {
  .payment-cart-success-container {
    .icon-container {
      svg {
        width: 80px;
        height: 80px;
      }
    }
    .total_money_container {
      .total_money_content {
        font-size: 17px;
        margin-right: unset;
      }
      .total_money_title {
        font-size: 16px;
      }
    }
    .title-text {
      font-size: 16px;
    }
    .voucher_item {
      flex-direction: column;
      p {
        margin-top: 4px;
      }
    }
  }
}

@media only screen and (max-width: 280px) {
  .payment-cart-success-container {
    .detail-text {
      font-size: 14px !important;
    }
  }
}
