.footer_container {
  background-color: #eabfb9;
  padding-top: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  .footer_content {
    width: 85%;

    background-color: #eabfb9;

    .footer_image {
      width: 130px;
      height: 110px;
    }
    .footer_content_social {
      margin-left: calc(min(2vw, 40px));

      display: flex;
      align-items: center;
      flex-direction: column;
      .icon_facebook {
        cursor: pointer;
        width: 13px;
        height: 22px;
      }
      .icon_twitter {
        margin-top: 20px;
        width: 25px;
        height: 22px;
      }
      .icon_instagram {
        cursor: pointer;
        margin-top: 20px;
        width: 25px;
        height: 22px;
      }
    }
    .footer_content_about {
      display: flex;
      flex-direction: column;
      .footer_content_about_content {
        cursor: pointer;
        margin-top: 20px;
        font-size: 16px;
        color: #000;
        font-family: 'Google Sans', sans-serif;
        font-weight: 400;
      }
      .footer_content_about_title {
        font-size: 16px;
        font-family: 'Google Sans', sans-serif;
        font-weight: 600;
      }
    }

    .image_registration_MIT {
      width: 157px;
      height: 59px;
    }
    .image_notification_MIT {
      width: 157px;
      height: 59px;
    }
    .download_app_title {
      font-size: 16px;
      font-weight: 600;
      font-family: 'Google Sans', sans-serif;
    }
    .download_app_ios {
      width: 135px;
      height: 35px;
    }
    .download_app_android {
      width: 135px;
      height: 35px;
    }
    .footer_content_bottom {
      margin-top: 20px;
      height: 54px;
      .footer_content_divider {
        height: 1px;
        background-color: black;
        margin-top: 12px;
      }
      .footer_content_bottom_copyright {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 16px;
        font-weight: 400;
        font-family: 'Google Sans', sans-serif;
        margin-top: 12px;
      }
    }
  }
}
