.breadcrumb_container {
  display: flex;
  justify-content: center;
  background-color: #f5f6f8;
  .breadcrumb_home {
    width: 85%;

    padding: 10px 0;
    margin: 0;
    background: none;
    box-shadow: none;
    height: auto;
    .home {
      color: #505f79;
      cursor: pointer;
    }
    .sub_breadcrumb {
      color: #505f79;
      cursor: pointer;
    }
  }

  .breadcrumb ol {
    display: inline-block;
  }

  .breadcrumb li {
    display: inline;
    font-size: 13px;
    line-height: 16px;
  }

  .breadcrumb li a {
    text-decoration: none;
    color: #505f79;
  }

  .breadcrumb li a:hover {
    color: #808ea7;
  }

  @media screen and (max-width: 480px) {
    .breadcrumb {
      padding: 10px 0 10px 10px;
    }
  }
}
