.delivery-address-modal {
  .title_container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .title {
    font-size: 1.8rem;
    font-weight: 700;
    font-family: 'Google Sans', sans-serif;
    margin-bottom: 20px;
  }

  .branch_piker_container {
    overflow: scroll;
    position: absolute;
    top: 30px;
    z-index: 2;
    max-height: 200px;
    padding: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    .branch_address {
      cursor: pointer;
      padding: 5px 10px 5px 10px;
    }
  }

  .input_container {
    cursor: pointer;
    flex: 0.7;
    background: #f6f9ff; /* #DFE2E7 */
    // width: 100%;
    padding: 10px;
    display: flex;
    position: relative;
    justify-content: center;
    border: 1px solid #dfe2e7;
    box-sizing: border-box;
    border-radius: 4px;
  }

  .autocomplete-dropdown-container {
    overflow: scroll;
    position: absolute;
    top: 40px;
    z-index: 2;
    width: 100%;
    max-height: 200px;
    // padding: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    .branch_address {
      cursor: pointer;
      padding: 5px 10px 5px 10px;
    }
  }

  .time_picker_container {
    overflow: scroll;
    position: absolute;
    top: 40px;
    z-index: 2;
    width: 100%;
    height: 200px;
    padding: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    .time_text {
      cursor: pointer;
      padding: 5px 100px 5px 100px;
    }
  }

  .submit_button {
    background: #ff4355;
    border-radius: 5px;
    font-weight: 400;
    font-family: 'Google Sans', sans-serif;
    font-size: 1rem;
    color: white;
    padding: 8px 40px;
    cursor: pointer;
  }

  .calendar_custom_container {
    max-width: 520px;
    z-index: 2;
    position: absolute;
    top: 40px;
    padding: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  .suggestion-item,
  .suggestion-item-active {
    padding: 8px;
    font-weight: 400;
    font-family: 'Google Sans', sans-serif;
    font-size: 1rem;
  }

  .start_delivery {
    width: 100%;
    background: transparent;
    font-weight: 400;
    font-family: 'Google Sans', sans-serif;
    font-size: 1rem;
  }

  .row-section {
    display: flex;
    align-items: center;
    padding-bottom: 1em;
    position: relative;

    .title-item {
      flex: 0.3;
      font-weight: 400;
      font-family: 'Google Sans', sans-serif;
      font-size: 1.2rem;
    }

    .select-item {
      flex: 0.7;
      height: 3em;
      background: #f6f9ff;
      border-radius: 4px;
      border: 1px solid #dfe2e7;
      outline: none;
    }
    .picker_item {
      flex: 0.7;
    }
    .input-item {
      border: 1px solid #dfe2e7;
      height: 3em;
      flex: 0.7;
      background: #f6f9ff;
      border-radius: 4px;
    }
  }
}

@media screen and (min-width: 960px) {
  .delivery-address-modal {
    width: 500px;
  }
}

@media screen and (max-width: 960px) {
  .delivery-address-modal {
    width: 400px;
  }
}

@media screen and (max-width: 600px) {
  .delivery-address-modal {
    width: 100%;
  }
}
