.mh-icon {
  line-height: 1;
  display: inline-block;

  &.ic-close {
    background: url('../../icons/ic-close.svg') center center / cover no-repeat;
    width: 16px;
    height: 16px;
  }

  &.ic-phone {
    background: url('../../icons/ic-phone.svg') center center / cover no-repeat;
    width: 18px;
    height: 24px;
  }

  &.ic-check {
    background: url('../../icons/ic-check.svg') center center / cover no-repeat;
    width: 46px;
    height: 34px;
  }

  &.ic-pencil {
    background: url('../../icons/ic-pencil.svg') center center / cover no-repeat;
    height: 16px;
    width: 16px;
  }
}
