.update-info-form {
  margin: 0px 40px;
  width: 50vw;
  display: flex;
  flex-direction: column;
  button {
    margin-right: auto;
    margin-left: auto;
  }

  .form-input {
    margin-top: 10px;
  }

  .form-label {
    width: 100%;
  }

  .update-info-form-input_avatar {
    width: 100%;
  }

  .form-widget {
    width: 100%;
  }

  .form_info_name,
  .form_info_email,
  .form_info_phone_number,
  .form_info_dob,
  .form_info_address,
  .form_info_identify {
    border-color: #d5d6db;
  }

  .form_info_phone_number {
    background-color: #f5f6f8;
  }

  .update-info-form-input {
    border-color: #d5d6db;
  }

  .form-input__readonly {
    background-color: #f5f6f8;
  }
  .title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 16px;
  }
  .subtitle {
    padding-right: 100px;
    padding-left: 100px;
    margin-bottom: 36px;
    margin-top: 8px;
    font-size: 15px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
  }
  .error {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    color: #ff4355;
    font-size: 15px;
  }
  .calendar_custom_container {
    max-width: 520px;
    position: absolute;
    top: 265px;
    padding: 10px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(141, 141, 141, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
}

@media only screen and (max-width: 959px) {
  .update-info-form {
    height: 500px;
    .title {
      text-align: center;
      font-size: 25px;
    }
    .form-builder {
      justify-content: center;
    }
  }
}
@media only screen and (max-width: 700px) {
  .update-info-form {
    width: 440px;
  }
}
@media only screen and (max-width: 600px) {
  .update-info-form {
    width: 420px;
  }
}
@media only screen and (max-width: 550px) {
  .update-info-form {
    width: 380px;

    .form-label {
      font-size: 18px;
    }
  }
}

@media only screen and (max-width: 500px) {
  .update-info-form {
    width: 310px;

    .success-add-to-cart-icon {
      height: 100px;
      width: 100px;
    }
    .title-text {
      font-size: 24px;
    }
    .confirm-button {
      width: 230px;
    }
    .btnText {
      font-size: 15px;
    }
  }
}

@media only screen and (max-width: 430px) {
  .update-info-form {
    width: auto;
    .avatar {
      width: 70px;
      height: 70px;
    }
    .custom_button {
      width: 200px;
    }
  }
}
@media only screen and (max-width: 420px) {
  .update-info-form {
    width: auto;
    .form-label {
      font-size: 16px;
    }
  }
}
@media only screen and (max-width: 411px) {
  .update-info-form {
    width: auto;
  }
}
@media only screen and (max-width: 360px) {
  .update-info-form {
  }
}
@media only screen and (max-width: 320px) {
  .update-info-form {
    .title {
      font-size: 18px !important;
      text-align: center;
    }
    .upload-btn {
      text-align: center;
    }
    .avatar {
      width: 60px;
      height: 60px;
    }
    .form-label {
      font-size: 16px;
    }
    .form-builder {
      justify-content: center;
    }
    .custom_button {
      width: 150px;
    }
  }
}
@media only screen and (max-width: 280px) {
  .update-info-form {
    .title {
      font-size: 15px !important;
    }
    .avatar {
      width: 40px;
      height: 40px;
    }
    .form-label {
      font-size: 14px;
    }
  }
}
