.cart-item-container {
  background: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 100%;
  padding: 20px;
  display: flex;
  margin-bottom: 20px;
  .image_url {
    width: 100%;
    height: 100%;
    border-radius: 5px;
  }
  .left-section {
    display: flex;
    flex-direction: row;
    .left-section-text {
      flex: 1;
      display: flex;

      margin-left: 20px;
      .dish-name-text {
        flex: 1;
        display: -webkit-box;
        -webkit-line-clamp: 2; /* number of lines to show */
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: 18px;
        font-family: 'Google Sans', sans-serif;
        font-weight: 400;
      }
    }
  }
  .delete-text {
    background-color: white;
    font-size: 18px;
    color: #fe5466;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
  }
  .content-image-section {
    position: absolute;
    top: 0;
    display: flex;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    align-items: flex-end;
    padding: 16px;
  }
  .image_merchant {
    width: 48px;
    height: 48px;
    border-radius: 24px;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.25);
  }
  .sale-price {
    font-size: 24px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    color: #fe5466;
  }
  .current-price-section {
    display: flex;
    flex-direction: row;
    .current-price {
      font-size: 15px;
      text-decoration: line-through;
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      padding-right: 8px;
    }
    .percent-value {
      padding-left: 8px;
      border-left: 2px solid;
      font-size: 15px;
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      margin-left: 8px;
    }
  }
  @media screen and (max-width: 600px) {
    .left-section-text {
      flex-direction: column;
    }
    .sale-price {
      margin-top: 20px;
    }
  }
}
