.login-success {
  max-width: 500px;
  text-align: center;
  .title {
    margin-top: 28px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 30px;
    color: #ff4355;
  }
  .subtitle {
    margin-bottom: 36px;
    margin-top: 8px;
    margin-left: 40px;
    margin-right: 40px;
    font-size: 20px;
    // max-width: 300px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    color: #243358;
  }
}

@media only screen and (max-width: 700px) {
  .login-success {
    width: 500px;
    .custom-button {
      min-width: unset;
    }
  }
}
@media only screen and (max-width: 650px) {
  .login-success {
    width: 430px;
    .success-icon {
      height: 130px;
      width: 130px;
    }
    .title {
      font-size: 23px !important;
    }
    .subtitle {
      font-size: 24px !important;
    }
    .title-button {
      font-size: 15px;
    }
    .custom-button {
      min-width: unset;
    }
  }
}

@media only screen and (max-width: 550px) {
  .login-success {
    width: 350px;
    .success-icon {
      height: 100px;
      width: 100px;
    }
    .title {
      font-size: 18px !important;
    }
    .subtitle {
      margin-top: 14px !important;
      margin-right: 15px !important;
      margin-left: 15px !important;
      font-size: 16px !important;
    }
    .title-button {
      font-size: 15px;
    }
    .custom-button {
      min-width: unset;
    }
  }
}

@media only screen and (max-width: 430px) {
  .login-success {
    width: 330px;
  }
  .custom_button {
  }
}
