@import 'src/styles/base';
.happy_bucket_item_container {
  cursor: pointer;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  .image-section {
    height: 183px;
    position: relative;
    .thumbnail {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  .content-section {
    display: flex;
    flex-direction: column;
    padding: 6px 20px 10px 20px;

    .eVoucher-name {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      font-size: 18px;
      font-family: 'Google Sans', sans-serif;
      font-weight: 700;
    }
    .eVoucher-description {
      margin-top: 10px;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
      font-size: 17px;
      overflow: hidden;
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
    }
  }
}
