$gap: 20px;
.form-builder {
  width: 100%;
  display: flex;
  flex-wrap: wrap;

  .form-label {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    font-size: 20px;
    width: 25%;
    flex-shrink: 0;
    text-align: left;
    color: black;
  }
  .form-input {
    width: 100%;
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    font-size: 20px;
  }
  .form-widget {
    margin-bottom: 20px;
  }

  .error {
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    color: #ff4355;
    font-size: 15px;
  }

  .cols {
    &-6 {
      width: 50%;
    }
  }
}
