.notification-item-container {
  background-color: #f6f9ff;
  border-radius: 4px;
  cursor: pointer;
  padding: 12px;
  margin-bottom: 8px;

  .title-txt {
    font-size: 15px;
    font-weight: bold;
    color: #000000;
  }

  .content-txt {
    font-size: 13px;
    color: #858688;
    font-weight: 400;
    height: 42px;
    margin-bottom: 8px;
    overflow: hidden;
    padding: 8px 0 8px 0;
  }

  .time-content {
    display: flex;
    align-items: center;

    .bell-icon {
      background-color: #ff4355;
      border-radius: 50%;
      width: 18px;
      height: 18px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .time-txt {
      font-size: 13px;
      color: #858688;
      font-weight: 400;
      padding-left: 8px;
    }
  }
}

.notification_read {
  background-color: white;
}
