.list_branch_base_modal {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
  .map_container {
    width: 100%;
    height: 100%;
  }
  .branch_container {
    position: relative;
    background-color: white;
    // height: 400px;
    display: flex;

    .title_container {
      background-color: #f5f6f8;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 18px;
    }
    .title {
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      text-align: center;
      font-weight: 600;
      font-size: 24px;
    }
    .count_branch {
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      text-align: center;
      font-weight: 400;
      font-size: 15px;
    }
    .branch_address {
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      font-weight: 400;
      flex: 1;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
      overflow: hidden;
      margin-right: 8px;
      font-size: 15px;
    }

    .branch_address_active {
      color: white;
    }
    .branch_distance {
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      text-align: center;
      font-weight: 400;
      font-size: 15px;
      color: #fe5466;
    }
    .branch_container {
      // display: flex;
      // align-items: center;
      // justify-content: space-between;
      padding: 18px;

      cursor: pointer;
    }
    .branch_container_active {
      background-color: #c4a732;
    }
    .list_branch_container {
      // width: 18vw;
      overflow: scroll;
    }
    .list_branch {
      overflow: scroll;
    }

    .image_merchant_marker {
      width: 44px;
      height: 44px;
      border-radius: 22px;
      margin-right: 10px;
      margin-bottom: 10px;
    }
    .merchant_name_marker {
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 17px;
    }
    .merchant_address_marker {
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
    }
    .title_marker {
      display: flex;
      align-items: center;
    }

    .map_container div:first-child {
      position: relative !important;
    }
    .icon_close {
      position: absolute;
      top: 20px;
      background-color: white;
      right: 15px;
      padding-top: 5px;
      width: 30px;
      height: 30px;
      border-radius: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  @media screen and (max-width: 600px) {
    .list_branch_container {
      height: 220px;
    }
    .map_container {
      height: 300px;
    }
  }
}
