.title-custom-container {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  .title-custom-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .title_content {
      padding-bottom: 10px;
      color: #000000;
      font-size: 2.5rem;
      font-weight: bold;
      &-icon {
        display: flex;
        justify-content: center;
        width: 100%;
        display: flex;
        align-self: center;
        background-size: cover;
      }
    }
  }
}
