.contact_container {
  background: #eabfb9;
  height: 32px;
  font-size: 14px;
  line-height: normal;
  color: #fff;

  .cart_badge {
    margin-top: 4px;
    display: flex;
    position: absolute;
    top: -5px;
    right: -7px;
    width: 20px;
    height: 15px;
    border-radius: 24px;
    align-items: center;
    justify-content: center;
    background-color: #fe5466;
  }
  .cart_ic {
    margin-top: 4px;
  }
  .container {
    width: 95%;
    margin: auto;
    display: table;
    height: 100%;

    .menu_icon {
      font-size: 25px;
    }
  }
  img {
    margin-right: 10px;
    margin-top: -5px;
    width: 16px;
  }
}

.grid_container {
  flex-wrap: nowrap !important;
}

header {
  width: 100%;
}

header .top {
  background: #eabfb9;
  height: 32px;
  font-size: 14px;
  line-height: normal;
  color: #fff;
}

.top_container {
  float: right;
  height: 100%;
  display: flex;
  align-items: center;

  .app {
    display: flex;
    align-items: center;
    .hotline {
      display: flex;
      align-items: center;
      font-weight: 300;
      font-size: 14px;
      font-family: 'Google Sans', sans-serif;
      b {
        font-weight: 700;
        font-size: 14px;

        font-family: 'Google Sans', sans-serif;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .top_container {
    height: 32px;
  }
}

@media screen and (max-width: 768px) {
  .top_container {
    height: 32px;
  }
}
