.confirm-modal-container {
  display: flex;
  max-width: 500px;
  min-height: 223px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .img-person {
    width: 120px;
    height: 120px;
    align-self: center;
  }
  .button-apply {
    height: 48px;
    width: 207px;
    border-radius: 6px;
  }
  .button-apply-title {
    color: var(--white);
    font-size: 16px;
    line-height: 20px;
  }
  .title-confirm {
    font-size: 23px;
    line-height: 29px;
    text-align: center;
    color: var(--primary-gray-green);
    margin-top: 16px;
    &.delete {
      color: var(--primary-pink);
    }
  }
  .des-confirm {
    font-size: 15px;
    line-height: 20px;
    text-align: center;
    color: #3a3a3c;
    margin: 16px 0px 20px 0px;
  }
}
