.custom_button {
  border-radius: 8px;
  padding: 12px 0;
  font-weight: bold;
  font-size: 18px;
  color: var(--white);
  width: 206px;
  background-color: #fe5466;
  line-height: normal;
  &:hover {
    background-color: rgba($color: #fe5466, $alpha: 0.7);
  }
}

@media only screen and (max-width: 430px) {
  .custom_button {
    width: 90%;
  }
}
