    url('../../fonts/GoogleSans-Regular.ttf') format('truetype');
  font-weight: 400;
}

@font-face {
  font-family: 'Google Sans';
  src: local('GoogleSansMedium'),
    url('../../fonts/GoogleSans-Medium.ttf') format('truetype');
