.input-group {
  display: flex;
  align-items: center;
  width: 100%;
}

.image-upload {
  display: flex;
  align-items: center;

  img {
    margin-right: 10px;
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 50%;
  }
  .upload-btn {
    position: relative;
    border-radius: 4px;
    background-color: #f5f6f8;
    border: 1px solid #d5d6db;
    color: #c4a732;
    font-size: 17px;
    padding: 5px 10px;
    cursor: pointer;
  }
  .file-upload {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
  }
}
