.login-success {
  max-width: 500px;
  text-align: center;
  .title {
    margin-top: 28px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 30px;
    color: #ff4355;
  }
  .subtitle {
    margin-bottom: 36px;
    margin-top: 8px;
    margin-left: 40px;
    margin-right: 40px;
    font-size: 20px;
    // max-width: 300px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    color: #243358;
  }
  .custom_button {
    border-radius: 8px;
    padding: 12px 0;
    font-weight: bold;
    font-size: 18px;
    color: var(--white);
    min-width: 206px;
    background-color: #fe5466;
    line-height: normal;
    &:hover {
      background-color: rgba($color: #fe5466, $alpha: 0.7);
    }
  }
}
