.charge-container {
  width: 100%;
  display: flex;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 12px;
  .price-section {
    border-radius: 8px;
    justify-content: space-between;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    .upper {
      display: flex;
      flex-direction: column;
      padding: 13px;
      .price-upper-content {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        .provisional-price {
          font-size: 15;
        }
      }
      .discount-content {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin-top: 16px;
        font-family: 'Google Sans', sans-serif;
        font-weight: 400;
      }
    }
    .price-content {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 13px;
      .total-price-text {
        font-size: 24px;
        color: #ff4355;
        font-family: 'Google Sans', sans-serif;
        font-weight: 400;
      }
    }
    .total-price-title {
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      font-size: 15px;
    }

    .temporary_money_title {
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      font-size: 15px;
    }

    .temporary_money_content {
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      font-size: 24px;
    }

    .discount_money_title {
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      font-size: 15px;
    }

    .discount_money_content {
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      color: #ff4355;
      font-size: 15px;
    }

    .line {
      width: 100%;
      height: 1px;
      background-color: #b8bbbf;
    }
  }
  .button-section {
    margin-top: 20px;
  }

  .apply-text {
    text-transform: none;
    font-size: 18px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
  }
}
