.discount-container {
  width: 100%;
  height: 79px;
  padding: 10px;
  background-color: #ffffff;
  border-radius: 8px;
  .discount-code {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 15px;
  }
  .content-section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    .input {
      width: 100%;
      height: 30px;
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      margin-left: 10px;
      border: 1px solid #d5d6db;
      border-radius: 3px 0px 0px 3px;
      padding-left: 10px;
    }
    .input:focus {
      outline: none;
    }
    .apply-text {
      font-size: 10px;
      color: white;
    }
  }
}
