.notification_container {
  position: relative;
  font-size: 20px;
  color: #505f79;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;

  .dropdown_bell_ic {
    margin-top: 6px;
    width: 24px;
    height: 24px;
    color: #fff;
    -moz-border-radius: 50%;
    border-radius: 50%;
    padding: 3px;
  }
  .dropdown_no_notifications {
    display: none;
    position: absolute;
    right: -130px;
    background-color: #ffffff;
    width: 470px;
    max-height: 500px;
    border: 1px solid #dfe2e7;
    -webkit-box-shadow: 0 10px 30px rgba(61, 56, 56, 0.1); /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); /* Firefox 3.5 - 3.6 */
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    z-index: 2;
    overflow: scroll;
    overflow-x: hidden;
    padding: 16px;
    p {
      // padding: 10px 16px 10px 16px;
      // font-size: 18px;
      font-family: 'Google Sans', sans-serif;
      // text-align: center;
    }
  }

  .dropdown_no_notifications::-webkit-scrollbar {
    width: 5px;
  }
  .dropdown_no_notifications::-webkit-scrollbar-thumb {
    background: #adb1b9;
    border-radius: 10px;
  }

  .badge {
    margin-top: 4px;
    display: flex;
    position: absolute;
    top: -5px;
    right: -7px;
    width: 20px;
    height: 15px;
    border-radius: 24px;
    align-items: center;
    justify-content: center;
    background-color: #fe5466;
    p {
      font-size: 12px;
      line-height: 20px;
      color: var(--white);
    }
  }

  @media screen and(max-width:600px) {
    .badge {
      top: 4px;
      right: 20px;
      margin-top: 0px;
    }
    .dropdown_bell_ic {
      margin-top: 4px;
      margin-left: 10px;
    }
    .dropdown_no_notifications {
      width: auto;
      left: 0px;
      right: 0px;
    }
  }
}

@media screen and (max-width: 600px) {
  .notification_container {
    position: initial;
  }
}

.notification_container:hover .dropdown_no_notifications {
  display: block;
}
