.header_container {
  nav {
    background: #fff;
    -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05); /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05); /* Firefox 3.5 - 3.6 */
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
  }

  nav .container {
    height: 100%;
  }

  .container {
    width: 90%;
    margin: auto;
    display: table;
    height: 100%;
  }

  .menu_container {
    padding: 12px 0;
  }

  .nav_container {
    display: flex;
    align-items: center;
  }
  .transaction_icon {
    width: 25px;
    height: 25px;
  }

  .logout_icon {
    width: 25px;
    height: 25px;
  }

  .logout_title {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    color: #000;
    opacity: 0.5;
    font-weight: 400;
  }

  .logout_icon {
    path {
      fill: #adb1b9;
    }
  }
  .logout_container {
    padding: 4px 10px 4px 10px;
    border-radius: 5px;
  }
  .logout_container:hover {
    background-color: #e7e8ea;
  }
  .logout_container:hover .logout_icon {
    path {
      fill: #253358;
    }
  }

  .credit_card_container {
    padding: 4px 10px 4px 10px;
    border-radius: 5px;
  }
  .credit_card_container:hover {
    background-color: #e7e8ea;
  }
  .credit_card_container:hover .credit_card_icon {
    path {
      fill: #253358;
    }
  }

  .credit_card_title {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    font-weight: 400;
    color: #000;
    opacity: 0.5;
  }

  .credit_card_icon:hover {
    path {
      fill: #253358;
    }
  }
  .credit_card_icon {
    path {
      fill: #adb1b9;
    }
  }

  .transaction_container {
    padding: 4px 10px 4px 10px;
    border-radius: 5px;
  }
  .transaction_container:hover {
    background-color: #e7e8ea;
  }
  .transaction_container:hover .transaction_icon {
    path {
      fill: #253358;
    }
  }

  .transaction_title {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    font-weight: 400;
    color: #000;
    opacity: 0.5;
  }

  .transaction_title:hover {
    margin-left: 10px;
    font-family: 'Google Sans', sans-serif;
    font-size: 15px;
    font-weight: 400;
    color: #243358;
    opacity: 1;
  }

  .box-user {
    position: relative;
  }

  .user_action_container {
    display: none;
    position: absolute;
    top: 24px;
    right: 6%;
    width: 100%;
    min-width: 200px;
    padding: 10px 4px 10px 4px;
    background-color: white;
    -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); /* Firefox 3.5 - 3.6 */
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    z-index: 1;
    // overflow: scroll;
    // overflow-x: hidden;
  }

  .user_container .user_action_container::after {
    content: '';
    position: absolute;
    top: 10px;
    right: 22px;
    background-color: transparent;
    margin-left: -10px;
    margin-top: -18px;
    z-index: 2;
    width: 12px;
    height: 12px;
    background-color: #ffff;
    transform: rotate(45deg);
    border-left: 1px solid #dfe2e7;
    border-top: 1px solid #dfe2e7;
    border-radius: 3px;
    -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); /* Firefox 3.5 - 3.6 */
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);

    // box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    // border-color: transparent transparent rgba(165, 161, 161, 0.336) transparent;
  }
  .user_container:hover .user_action_container {
    display: block;
  }
  .menu_item_container {
    display: flex;
    // margin-right: 15px;
    text-align: center;
    cursor: pointer;
    .category_logo {
      height: 35px;
      width: 35px;
      -webkit-border-radius: 4px;
      -moz-border-radius: 4px;
      border-radius: 4px;
      object-fit: contain;
    }
  }

  .menu_item_content {
    flex-direction: column;
    padding: 15px 0;
    display: flex;
    flex: 1;
    width: 100%;
    align-items: center;
  }
  .menu_name {
    width: 100%;
    color: black;
    font-family: 'Google Sans', sans-serif;
    font-size: 13px;
    margin-top: 12px;
    line-height: 16px;
    font-weight: 700;
  }

  .menu_item_btn {
    cursor: pointer;
  }

  .logo_container {
    display: table;
    height: 100%;
  }

  .logo {
    display: table-cell;
    vertical-align: middle;
  }

  .logo img {
    width: 40px;
    height: 40px;
  }

  .image_logo_home {
    width: 105px;
    height: 40px;
  }

  .logo span {
    display: inline-block;
    font-size: 17px;
    font-weight: bold;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.326923px;
    vertical-align: top;
    padding-top: 10px;
  }
  .user_container {
    display: flex;
    align-items: center;
    .avatar_ic {
      align-self: center;
      width: 20px;
      height: 20px;
    }
  }

  .search_temp_container {
    margin-right: 5vw;
  }
  .user_name {
    max-width: 100px;
    display: -webkit-box;
    -webkit-line-clamp: 1; /* number of lines to show */
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin-left: 10px;
    font-size: 16px;
    line-height: 18px;
    color: #1f2d50;
    font-weight: 600;
    font-family: 'Google Sans', sans-serif;
  }

  .account_container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    padding: 0px 20px;
    .cart_ic {
      width: 20px;
      height: 20px;
    }
    .regiter_or_login {
      cursor: pointer;
      padding-left: 8px;
      display: flex;
      flex-direction: column;
      p {
        color: #1f2d50;
        font-weight: 600;
        font-size: 14px;
        font-family: 'Google Sans', sans-serif;
        line-height: 18px;
      }
    }
    .box-switch-language {
      display: flex;
      .text-languages {
        font-size: 18px;
        line-height: 28px;
        margin: 0px 6px;
        font-weight: 550;
        opacity: 0.2;
        &.active {
          opacity: 1;
          color: #1f2d50;
        }
      }
    }
  }

  .avatar {
    height: 24px;
    width: 24px;
    padding: 7px 0;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    -o-object-fit: cover;
    object-fit: cover;
  }

  .user span,
  .user .user_info {
    display: inline-block;
    font-size: 15px;
    font-weight: 600;
    line-height: normal;
    text-align: center;
    letter-spacing: 0.326923px;
    vertical-align: top;
    padding: 10px;
  }

  .user .user_info span {
    margin-right: 5px;
    padding: 0;
  }

  .user .user_action {
    display: inline-block;
    font-size: 15px;
    font-weight: 600;
    line-height: normal;
    text-align: center;
    letter-spacing: 0.326923px;
    vertical-align: top;
    padding: 0 10px;
  }

  .user .user_action button {
    padding: 0;
    background-color: #fff;
    font-size: 15px;
    text-transform: none;
    color: #162a4c;
    cursor: pointer;
  }

  .user .user_action button span {
    font-weight: normal;
    letter-spacing: inherit;
  }

  .dropdown {
    position: relative;
    display: contents;
    .dropdown_content5 {
      display: none;
      position: absolute !important;
      right: 0;
      background-color: #f9f9f9;
      min-width: 160px;
      width: 250px;
      -webkit-border-radius: 4px;
      -moz-border-radius: 4px;
      border-radius: 4px;
      -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
      -moz-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); /* Firefox 3.5 - 3.6 */
      box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
      z-index: 1;
    }
  }

  .dropdown2 {
    position: relative;
    display: block;
  }

  .dropdown_content::before,
  .dropdown_content2::before {
    content: '';
    position: absolute;
    right: 11px;
    top: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 10px 10px 10px;
    border-color: transparent transparent #f9f9f9 transparent;
    z-index: 9999;
  }

  .dropdown_content2 {
    display: none;
    position: absolute !important;
    right: 0;
    background-color: #f9f9f9;
    min-width: 160px;
    width: 250px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */
    -moz-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); /* Firefox 3.5 - 3.6 */
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    z-index: 1;
  }

  .dropdown_content2 {
    position: absolute !important;
  }

  .dropdown_content .fa,
  .dropdown_content2 .fa {
    margin-right: 10px;
    width: 15px;
  }

  .link {
    cursor: pointer;
    position: relative;
    .badge {
      display: flex;
      position: absolute;
      top: -8px;
      right: -7px;
      width: 20px;
      height: 15px;
      border-radius: 24px;
      align-items: center;
      justify-content: center;
      background-color: #fe5466;
      p {
        font-size: 12px;
        line-height: 20px;
        color: var(--white);
      }
    }
  }

  .dropdown_content .link,
  .dropdown_content2 .link {
    color: #162a4c;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    text-transform: none;
  }

  .dropdown_content .link:hover {
    background-color: #f1f1f1;
  }

  .dropdown:hover .dropdown_content,
  .dropdown2:hover .dropdown_content2 {
    display: block;
    max-width: 200px;
  }

  .dropdown:hover .dropdown_content {
    margin-top: 1px;
  }

  .dropdown2:hover .dropdown_content2 {
    margin-top: 10px;
  }
  .dropdown_content2 .category_logo {
    margin-right: 5px;
  }

  .dropdown .menu_name,
  .dropdown2 .menu_name {
    padding-left: 5px;
  }

  .root_list_item {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }

  .menu_button {
    padding: 5px !important;
  }

  @media screen and (max-width: 480px) {
    .top_container {
      height: 32px;
    }
  }

  @media screen and (max-width: 768px) {
    .top_container {
      height: 32px;
    }

    .account .user {
      padding-left: 0;
    }
  }
  @media only screen and (max-width: 900px) {
    .menu_item_container {
      width: 100%;
    }
    .menu_item_content {
      flex-direction: column;
    }
    .menu_name {
      font-size: 13px;
      margin-top: 10px;
    }
    .search_temp_container {
      margin-right: 10px;
    }
    .account_container {
      padding: 0;
    }
  }
  @media screen and(max-width:960px) {
    .user_name {
      width: 40px;
    }
  }
}
